import React, {useEffect, useRef} from 'react';
import axios from 'axios';
import {helpURL, hideSpinner,showSpinner} from "src/utilities/utilityFunctions";
import {updateAppSettings} from "src/actions/foundationActions";
import store from 'src/store';

const processHelpContent = (evt) => {
    store.dispatch(updateAppSettings({showDefaultModal:true,helpText:evt.data.content,modalModuleName:"KIM/structure/HelpIcon/ShowHelpContent",modalTitle:evt.data.title}));

};

const failHelpContent = (props,evt)=>{
    console.dir("fail");
    console.dir(evt);
};


const HelpIcon = (props) => {

    const hexRef = useRef();

    useEffect(()=>{
        console.dir("helpicon: " + process.env.REACT_APP_HELP_ICON_VISIBLE_COMPONENT)
    },[])

    const openHelpArticle = (slug) => {
        showSpinner();
        hexRef.current.style.display = "inline-block";
        axios.get(helpURL() + "/"+slug + ".json").then((evt) => {hideSpinner();processHelpContent( evt)}).catch((evt) => {hideSpinner();failHelpContent(evt)}).finally(() => {
                        hexRef.current.style.display = "none";
                    });
    };
    return (
        <div style={{display:"inline-block",position:"absolute"}}>
            <span className="ml-2" onClick={()=>openHelpArticle(props.slug)} id="helpIcon">Help</span>
            <div ref={hexRef} style={{display:"none",transform:"matrix(0.9, 0, 0, 0, 0, -25)"}}></div>
        </div>
    )
};

export default HelpIcon;
